import React, { useState } from 'react';

function ProductInfo() {

  const [infoVisible, setInfoVisible] = useState(true);

  function infoToggleHandler(e) {
    console.log(e.target.classList.contains("tab__item-1"));

    if (e.target.classList.contains("tab__item-1") && (infoVisible === false)) {
      setInfoVisible(true);
    } else if (e.target.classList.contains("tab__item-2") && (infoVisible === true)) {
      setInfoVisible(false);
    } else {
      return
    }
  }


  return (
    <div className="product__description__container">
      <div className="description__tabs__wrapper">
        <div className={`description__tabs__item tab__item-1 ${infoVisible ? "active" : ""}`} onClick={infoToggleHandler}></div>
        <div className={`description__tabs__item tab__item-2 ${infoVisible ? "" : "active"}`} onClick={infoToggleHandler}></div>
      </div>
      <div className="description__content__wrapper">
        <div className={`description__content__item content__item-1 ${infoVisible ? "active" : ""}`}>
          <div className="content__inner__title">  </div>
          <p className="content__text">  .      
                      .</p>
          <p className="content__text"><span className="content__green"> :</span>, 480 /</p>
          <p className="content__text"><span className="content__green"> :</span> 
          </p>
          <p className="content__text"><span className="content__green"> :</span>0,2-0,6 /</p>
          <p className="content__text"><span className="content__green"> :</span>2.</p>
          <p className="content__text"><span className="content__green"> :</span>2 </p>
          <div className="content__inner__title"></div>
          <p className="content__text">       (
            )         ;
          </p>
          <ul>
            <li className="conent__list__item">  ,     
              ,     ;
            </li>
            <li className="conent__list__item">         
                  ,     ,   
                   ;
            </li>
            <li className="conent__list__item">      ;
            </li>
            <li className="conent__list__item">    ;
            </li>
            <li className="conent__list__item">     ..  
              ;
            </li>
            <li className="conent__list__item">    (, , )
                        
              ;
            </li>
            <li className="conent__list__item">  ,   100   
               , , , , , , ,    .
            </li>
          </ul>
          <div className="content__inner__title">:</div>
          <ul>
            <li className="conent__list__item">      
              :  ,   ,   ;
            </li>
            <li className="conent__list__item">     ,  
                 ;
            </li>
            <li className="conent__list__item">      
                 -  ;
            </li>
            <li className="conent__list__item">       , 
                   ;
            </li>
            <li className="conent__list__item">      ,  
                ,       ,  
               ;
            </li>
            <li className="conent__list__item">    30-60      
                      8-10 ,    
                 ;
            </li>
            <li className="conent__list__item">       
                 .</li>
          </ul>
        </div>
        <div className={`description__content__item content__item-2 ${infoVisible ? "" : "active"}`}>
          <form className="description__content__form">
            <div className="content__input__wrapper">
              <input type="text" className="content__input__name" placeholder="" />
              <input type="text" className="content__input__mail" placeholder="" />
            </div>
            <textarea name="content__input" className="content__textarea" placeholder=""></textarea>
            <button className="content__submit__btn"> </button>
          </form>
        </div>
      </div>
    </div >
  )
}

export default ProductInfo